"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { formatAddress } from "@/lib/utils"

export default function OverviewTab({
  campaignInfo,
  isOwner,
  isBeforeStart,
  isDuringVoting,
  isAfterEnd,
  proposals,
  candidates,
}) {
  if (!campaignInfo) return null

  return (
    <div className="space-y-6">
      {/* Campaign Details */}
      <Card>
        <CardHeader>
          <CardTitle>Campaign Details</CardTitle>
          <CardDescription>Information about this voting campaign</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-1">
                <p className="text-sm font-medium text-muted-foreground">Campaign Type</p>
                <div className="flex flex-wrap items-center gap-2">
                  <Badge variant="outline">
                    {campaignInfo.votingType === 0 ? "Candidate-Based" : "Proposal-Based"}
                  </Badge>
                  <Badge variant="outline">
                    {campaignInfo.restriction === 0
                      ? "Open To All"
                      : campaignInfo.restriction === 1
                        ? "Limited"
                        : "Registration Required"}
                  </Badge>
                  <Badge variant="outline">{campaignInfo.resultType === 0 ? "Public Results" : "Hidden Results"}</Badge>
                </div>
              </div>

              <div className="space-y-1">
                <p className="text-sm font-medium text-muted-foreground">Creator</p>
                <p className="text-sm">
                  {isOwner ? "You" : formatAddress(campaignInfo.creator)}
                  {isOwner && <Badge className="ml-2 bg-primary/20">Creator</Badge>}
                </p>
              </div>
            </div>

            <div className="space-y-1">
              <p className="text-sm font-medium text-muted-foreground">Description</p>
              <p className="text-sm whitespace-pre-wrap">{campaignInfo.description}</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 pt-2">
              <div>
                <p className="text-xs font-medium text-muted-foreground">Start Date</p>
                <p className="text-sm">{new Date(campaignInfo.startTime).toLocaleString()}</p>
              </div>

              <div>
                <p className="text-xs font-medium text-muted-foreground">End Date</p>
                <p className="text-sm">{new Date(campaignInfo.endTime).toLocaleString()}</p>
              </div>

              <div>
                <p className="text-xs font-medium text-muted-foreground">Total Votes</p>
                <p className="text-sm">{campaignInfo.totalVotes}</p>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Options Preview */}
      <Card>
        <CardHeader>
          <CardTitle>
            {campaignInfo.votingType === 1 ? `Proposals (${proposals.length})` : `Candidates (${candidates.length})`}
          </CardTitle>
          <CardDescription>
            {campaignInfo.votingType === 1
              ? "Available proposals in this campaign"
              : "Registered candidates in this campaign"}
          </CardDescription>
        </CardHeader>
        <CardContent>
          {campaignInfo.votingType === 1 ? (
            <div className="space-y-3">
              {proposals.length === 0 ? (
                <p className="text-muted-foreground text-sm">No proposals have been added to this campaign.</p>
              ) : (
                proposals.map((proposal) => (
                  <div key={proposal.id} className="p-3 border rounded-md">
                    <div className="flex justify-between">
                      <h3 className="font-medium">Proposal {proposal.id + 1}</h3>
                      {isAfterEnd && <Badge variant="outline">{proposal.voteCount} votes</Badge>}
                    </div>
                    <p className="text-sm mt-1 text-muted-foreground">{proposal.content}</p>
                  </div>
                ))
              )}
            </div>
          ) : (
            <div className="space-y-3">
              {candidates.length === 0 ? (
                <p className="text-muted-foreground text-sm">No candidates have registered for this campaign.</p>
              ) : (
                candidates.map((candidate) => (
                  <div key={candidate.id} className="p-3 border rounded-md">
                    <div className="flex justify-between">
                      <h3 className="font-medium">{candidate.name}</h3>
                      {isAfterEnd && <Badge variant="outline">{candidate.voteCount} votes</Badge>}
                    </div>
                    <p className="text-xs text-muted-foreground">{formatAddress(candidate.address)}</p>
                    <p className="text-sm mt-1 text-muted-foreground">{candidate.statement}</p>
                  </div>
                ))
              )}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Campaign Rules */}
      <Card>
        <CardHeader>
          <CardTitle>Campaign Rules</CardTitle>
          <CardDescription>Important information about this campaign</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div>
              <h4 className="font-medium mb-2">Voting Eligibility</h4>
              <p className="text-sm text-muted-foreground">
                {campaignInfo.restriction === 0
                  ? "This campaign is open to all. Anyone can vote without registration."
                  : campaignInfo.restriction === 1
                    ? "This campaign has limited registration. Only a specific number of voters can register."
                    : "This campaign requires registration. You must register before you can vote."}
              </p>
            </div>

            <div>
              <h4 className="font-medium mb-2">Results Visibility</h4>
              <p className="text-sm text-muted-foreground">
                {campaignInfo.resultType === 0
                  ? "Results will be publicly visible to everyone after the campaign ends."
                  : "Results will only be visible to the campaign creator."}
              </p>
            </div>

            <div>
              <h4 className="font-medium mb-2">Voting Process</h4>
              <p className="text-sm text-muted-foreground">
                {campaignInfo.votingType === 0
                  ? "This is a candidate-based campaign. You can vote for one candidate."
                  : "This is a proposal-based campaign. You can vote for one proposal."}
              </p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
