"use client"

import { useState, useEffect } from "react"
import { useParams, useRouter } from "next/navigation"
import Link from "next/link"
import { useContract } from "@/context/contract-context"
// Assuming useSubgraph hook is primarily used for refetchEvents here
import { useSubgraph } from "@/context/graphql/queries" // Corrected path assumption
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Skeleton } from "@/components/ui/skeleton"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { ArrowLeft, CalendarClock, Clock, Users, AlertCircle, RefreshCw } from "lucide-react" // Added RefreshCw
import { formatDate, formatAddress } from "@/lib/utils" // Added formatAddress
import RegisterTab from "./register-tab"
import ResultsTab from "./results-tab"
import CampaignCountdown from "./countdown-timer"
import { ApolloClient, InMemoryCache, gql } from '@apollo/client'
import OverviewTab from "./overview-tab"
import VoteTab from "./vote-tab"

// --- Define GraphQL Client and Query outside the component ---
const client = new ApolloClient({
  uri: 'https://api.studio.thegraph.com/query/101223/polytix/version/latest', // Ensure this URI is correct
  cache: new InMemoryCache(),
  // Consider default options if you frequently need non-cached data
  // defaultOptions: {
  //   watchQuery: { fetchPolicy: 'network-only' },
  //   query: { fetchPolicy: 'network-only' },
  // }
});

// Define GraphQL query for campaign details outside the component
const CAMPAIGN_DETAILS_QUERY = gql`
  query CampaignDetails($id: String!) { # Use String if campaignId can be large
    # Fetch CampaignCreated for basic immutable info
    campaignCreateds(where: {campaignId: $id}, first: 1) {
      creator
      votingType
      restriction
      startTime
      endTime
      blockTimestamp
      # If description is emitted with event and stored, fetch it here
      # description
    }
    # Fetch latest CampaignStatusChanged for status
    campaignStatusChangeds(where: {campaignId: $id}, orderBy: blockTimestamp, orderDirection: desc, first: 1) {
      status
    }
    # Fetch all votes for the campaign
    voteCasts(where: {campaignId: $id}) {
      voter
      targetId # ID of the candidate or proposal voted for
    }
    # Fetch all registrations for the campaign
    voterRegistereds(where: {campaignId: $id}) {
      voter
      tokenId # Might be useful
    }
    # Fetch candidates if votingType is 0
    candidateAddeds(where: {campaignId: $id}, orderBy: candidateId, orderDirection: asc) {
      candidateId
      candidateAddress
      name
      # statement # Fetch if stored in subgraph
    }
    # Fetch proposals if votingType is 1
    proposalAddeds(where: {campaignId: $id}, orderBy: proposalId, orderDirection: asc) {
      proposalId
      content
    }
    # --- Potentially fetch derived Campaign entity if available ---
    # campaigns(where: { campaignId: $id }, first: 1) {
    #   id
    #   description # Get description from here if stored on Campaign entity
    #   resultType # Get resultType from here if stored
    #   totalVotes # Get counts from here if aggregated
    #   registeredVoterCount
    #   status
    # }
  }
`
// --- End of definitions outside component ---


export default function CampaignDetailsPage() {
  const params = useParams()
  const router = useRouter()
  const campaignId = params.id
  const { contract, address, isConnected, connectWallet } = useContract()
  // Assuming useSubgraph is primarily for refetching after contract interactions
  const { refetchEvents } = useSubgraph(); // Use the correct hook/context

  // State variables
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [campaignInfo, setCampaignInfo] = useState(null);
  const [proposals, setProposals] = useState([]);
  const [candidates, setCandidates] = useState([]);
  const [userRegistered, setUserRegistered] = useState(false);
  const [userVoted, setUserVoted] = useState(false);
  const [isOwner, setIsOwner] = useState(false);
  const [activeTab, setActiveTab] = useState("overview");

  // Time-based states
  const [isBeforeStart, setIsBeforeStart] = useState(false);
  const [isDuringVoting, setIsDuringVoting] = useState(false);
  const [isAfterEnd, setIsAfterEnd] = useState(false);

  // Fetch campaign data using GraphQL and contract calls
  const fetchCampaignData = async () => {
    if (!campaignId) {
        setError("Campaign ID is missing.");
        setLoading(false);
        return;
    }

    try {
      setLoading(true);
      setError(null);
      setCampaignInfo(null); // Reset previous data

      // Fetch campaign data using GraphQL
      const { data, error: queryError } = await client.query({
        query: CAMPAIGN_DETAILS_QUERY,
        variables: { id: String(campaignId) }, // Ensure ID is passed as string if needed
        fetchPolicy: "network-only" // Fetch fresh data from the subgraph
      });

      if (queryError) throw queryError; // Throw Apollo error

      if (!data || !data.campaignCreateds || data.campaignCreateds.length === 0) {
        setError("Campaign not found in subgraph.");
        setLoading(false);
        return;
      }

      const campaignCreatedEvent = data.campaignCreateds[0];
      const voteCasts = data.voteCasts || [];
      const voterRegistrations = data.voterRegistereds || [];
      const candidateData = data.candidateAddeds || [];
      const proposalData = data.proposalAddeds || [];
      const statusChanges = data.campaignStatusChangeds || [];

      // --- Time Calculations ---
      const now = Math.floor(Date.now() / 1000);
      const startTime = Number(campaignCreatedEvent.startTime);
      const endTime = Number(campaignCreatedEvent.endTime);
      const beforeStart = now < startTime;
      const duringVoting = now >= startTime && now < endTime;
      const afterEnd = now >= endTime;
      setIsBeforeStart(beforeStart);
      setIsDuringVoting(duringVoting);
      setIsAfterEnd(afterEnd);

      // --- Process Subgraph Data ---
      let campaignDescription = `Campaign #${campaignId}`; // Default description
      let campaignResultType = 0; // Default result type

      // If description/resultType were fetched via subgraph (e.g., from Campaign entity or CampaignCreated)
      // Update defaults here, e.g.:
      // if (data.campaigns && data.campaigns.length > 0) {
      //     campaignDescription = data.campaigns[0].description || campaignDescription;
      //     campaignResultType = data.campaigns[0].resultType !== null ? Number(data.campaigns[0].resultType) : campaignResultType;
      // } else if (campaignCreatedEvent.description) { // If description on CampaignCreated
      //     campaignDescription = campaignCreatedEvent.description;
      // }

      // Process proposals or candidates based on voting type
      let processedProposals = [];
      let processedCandidates = [];
      if (Number(campaignCreatedEvent.votingType) === 1) { // Proposal-based
        processedProposals = proposalData.map(proposal => {
          const voteCount = voteCasts.filter(vote => String(vote.targetId) === String(proposal.proposalId)).length;
          return {
            id: Number(proposal.proposalId),
            content: proposal.content,
            voteCount: voteCount
          };
        });
        setProposals(processedProposals);
      } else { // Candidate-based
        processedCandidates = candidateData.map(candidate => {
          const voteCount = voteCasts.filter(vote => String(vote.targetId) === String(candidate.candidateId)).length;
          return {
            id: Number(candidate.candidateId),
            name: candidate.name,
            address: candidate.candidateAddress,
            statement: candidate.statement || "Statement from Subgraph (if available)", // Use subgraph statement if fetched
            voteCount: voteCount
          };
        });
        setCandidates(processedCandidates);
      }

      // --- Combine Base Campaign Info (from Subgraph) ---
      let baseCampaignData = {
        id: campaignId,
        votingType: Number(campaignCreatedEvent.votingType),
        restriction: Number(campaignCreatedEvent.restriction),
        resultType: campaignResultType, // Use value derived/fetched from subgraph initially
        creator: campaignCreatedEvent.creator,
        description: campaignDescription, // Use value derived/fetched from subgraph initially
        startTime: startTime * 1000, // Convert to milliseconds
        endTime: endTime * 1000, // Convert to milliseconds
        status: statusChanges.length > 0 ? Number(statusChanges[0].status) : (afterEnd ? 2 : (duringVoting ? 1 : 0)), // Use latest status or derive from time
        totalVotes: voteCasts.length,
        itemCount: Number(campaignCreatedEvent.votingType) === 1 ? proposalData.length : candidateData.length,
        registeredVoterCount: voterRegistrations.length,
      };

       // --- User Status Checks (only if connected) ---
       let isRegistered = false;
       let hasVoted = false;
       const isCampaignOwner = isConnected && address && campaignCreatedEvent.creator.toLowerCase() === address.toLowerCase();
       setIsOwner(isCampaignOwner);

       if (isConnected && address) {
         // Check registration status
         if (baseCampaignData.restriction === 2) { // Registration required
           isRegistered = voterRegistrations.some(
             reg => reg.voter.toLowerCase() === address.toLowerCase()
           );
         } else { // Open or Limited (no on-chain registration event for voters)
           isRegistered = true; // Assume allowed to vote if campaign is open/limited
         }
         setUserRegistered(isRegistered);

         // Check voting status
         hasVoted = voteCasts.some(
           vote => vote.voter.toLowerCase() === address.toLowerCase()
         );
         setUserVoted(hasVoted);
       } else {
           setUserRegistered(false);
           setUserVoted(false);
       }

      // --- Fetch Additional Data from Contract (if needed and connected) ---
      if (contract) {
        try {
          // Get potentially missing basic info (like description, resultType if not in subgraph)
          const basicInfo = await contract.getCampaignBasicInfo(campaignId);
          baseCampaignData.description = basicInfo.description || baseCampaignData.description; // Prefer contract description
          baseCampaignData.resultType = basicInfo.resultType !== undefined ? Number(basicInfo.resultType) : baseCampaignData.resultType; // Prefer contract result type

          // Get candidate statements if not in subgraph
          if (baseCampaignData.votingType === 0 && processedCandidates.length > 0) {
             const updatedCandidates = [...processedCandidates]; // Create copy
             for (let i = 0; i < updatedCandidates.length; i++) {
               // Only fetch if statement wasn't already retrieved from subgraph
               if (!updatedCandidates[i].statement || updatedCandidates[i].statement.includes("Subgraph")) {
                 try {
                    const candidateDetails = await contract.getCandidate(campaignId, updatedCandidates[i].id);
                    updatedCandidates[i].statement = candidateDetails.statement || "No statement provided";
                 } catch (candidateErr) {
                    console.warn(`Error fetching statement for candidate ${updatedCandidates[i].id}:`, candidateErr);
                    updatedCandidates[i].statement = "Could not load statement";
                 }
               }
             }
             setCandidates(updatedCandidates); // Update state with fetched statements
           }

        } catch (err) {
          console.warn("Warning: Error fetching additional contract data:", err);
          // Proceed with potentially incomplete data from subgraph + defaults
        }
      }

      // --- Final State Updates ---
      setCampaignInfo(baseCampaignData);

      // Set initial active tab based on campaign status and user state
      if (afterEnd) {
        setActiveTab("results");
      } else if (duringVoting && isRegistered && !hasVoted) {
        setActiveTab("vote");
      } else if (beforeStart && baseCampaignData.restriction === 2 && !isRegistered) { // Only show register tab if needed
        setActiveTab("register");
      } else {
          setActiveTab("overview"); // Default to overview
      }

    } catch (err) {
      console.error("Error fetching campaign details:", err);
      // Check for specific Apollo errors if needed
      if (err.networkError) {
          setError("Network error: Could not reach the subgraph.");
      } else if (err.graphQLErrors) {
          setError(`GraphQL error: ${err.graphQLErrors.map(e => e.message).join(', ')}`);
      } else {
          setError("Failed to load campaign details. Please check the console and try again.");
      }
    } finally {
      setLoading(false);
    }
  };


  // Fetch data on mount and when dependencies change
  useEffect(() => {
    fetchCampaignData();
  }, [campaignId, contract, address, isConnected]); // Rerun if these change


  // Handle registration success (Called from RegisterTab)
  const handleRegistrationSuccess = async () => {
    setUserRegistered(true);

    // Optimistically update count, or re-fetch for accuracy
    setCampaignInfo((prev) => ({
        ...prev,
        registeredVoterCount: (prev?.registeredVoterCount ?? 0) + 1,
    }));

    // Trigger subgraph refresh to pick up the new event eventually
    if (refetchEvents) refetchEvents();

    // Optionally, fully refetch data for maximum accuracy after a delay
    // setTimeout(fetchCampaignData, 2000); // e.g., refetch after 2s

    setActiveTab("overview"); // Switch tab
  };

  // Handle vote success (Called from VoteTab)
  const handleVoteSuccess = async (votedItemId) => { // Accept voted item ID
    setUserVoted(true);

    // Optimistically update counts
    setCampaignInfo((prev) => ({
      ...prev,
      totalVotes: (prev?.totalVotes ?? 0) + 1,
    }));

    // Optimistically update item vote count
    if (campaignInfo?.votingType === 1) { // Proposal
        setProposals(prevProposals => prevProposals.map(p =>
            String(p.id) === String(votedItemId) ? { ...p, voteCount: p.voteCount + 1 } : p
        ));
    } else { // Candidate
        setCandidates(prevCandidates => prevCandidates.map(c =>
            String(c.id) === String(votedItemId) ? { ...c, voteCount: c.voteCount + 1 } : c
        ));
    }

    // Trigger subgraph refresh
    if (refetchEvents) refetchEvents();

    // Optionally, fully refetch data after a delay
    // setTimeout(fetchCampaignData, 2000);

    setActiveTab("results"); // Switch tab
  };

  // Get status message based on campaign state and user status
  const getStatusMessage = () => {
    if (!campaignInfo) return null;

    // Determine badge styles based on state
    const alertBaseClasses = "border";
    const alertTextClasses = "flex items-center";

    if (isAfterEnd) {
      return (
        <Alert className={`${alertBaseClasses} bg-purple-50 dark:bg-purple-950/20 border-purple-200 dark:border-purple-900/50`}>
          <AlertDescription className={alertTextClasses}>
            📊 This campaign has ended. Final results are available below.
          </AlertDescription>
        </Alert>
      );
    }

    if (isBeforeStart) {
      if (campaignInfo.restriction === 0) { // Open
        return (
          <Alert className={`${alertBaseClasses} bg-blue-50 dark:bg-blue-950/20 border-blue-200 dark:border-blue-900/50`}>
            <AlertDescription className={alertTextClasses}>
              🗳️ This campaign is open to all. Voting starts soon!
            </AlertDescription>
          </Alert>
        );
      } else if (!isConnected) { // Registration required, but wallet not connected
          return (
            <Alert className={`${alertBaseClasses} bg-amber-50 dark:bg-amber-950/20 border-amber-200 dark:border-amber-900/50`}>
              <AlertDescription className={alertTextClasses}>
                🔐 This campaign requires registration. <Button variant="link" className="p-0 h-auto ml-1" onClick={connectWallet}>Connect wallet</Button> to check status or register.
              </AlertDescription>
            </Alert>
          );
      } else if (campaignInfo.restriction === 2 && !userRegistered) { // Registration required, not registered
        return (
          <Alert className={`${alertBaseClasses} bg-amber-50 dark:bg-amber-950/20 border-amber-200 dark:border-amber-900/50`}>
            <AlertDescription className={alertTextClasses}>
              🔐 This campaign requires registration. Go to the 'Register' tab to participate.
            </AlertDescription>
          </Alert>
        );
      } else { // Registered (or Limited access assumed ok)
        return (
          <Alert className={`${alertBaseClasses} bg-green-50 dark:bg-green-950/20 border-green-200 dark:border-green-900/50`}>
            <AlertDescription className={alertTextClasses}>
              🎉 You are eligible for this campaign! Voting will start soon.
            </AlertDescription>
          </Alert>
        );
      }
    }

    if (isDuringVoting) {
        if (!isConnected) { // Voting active, but wallet not connected
             return (
               <Alert className={`${alertBaseClasses} bg-blue-50 dark:bg-blue-950/20 border-blue-200 dark:border-blue-900/50`}>
                 <AlertDescription className={alertTextClasses}>
                   🗳️ Voting is active! <Button variant="link" className="p-0 h-auto ml-1" onClick={connectWallet}>Connect wallet</Button> to check eligibility and vote.
                 </AlertDescription>
               </Alert>
             );
        } else if (userVoted) { // Already voted
            return (
            <Alert className={`${alertBaseClasses} bg-green-50 dark:bg-green-950/20 border-green-200 dark:border-green-900/50`}>
                <AlertDescription className={alertTextClasses}>
                🙌 Thanks for voting! Your vote has been securely recorded.
                </AlertDescription>
            </Alert>
            );
        } else if (campaignInfo.restriction === 2 && !userRegistered) { // Registration required, but not registered
            return (
            <Alert className={`${alertBaseClasses} bg-amber-50 dark:bg-amber-950/20 border-amber-200 dark:border-amber-900/50`}>
                <AlertDescription className={alertTextClasses}>
                🍿 Registration was required for this campaign, but you did not register. You can view live results.
                </AlertDescription>
            </Alert>
            );
        } else { // Eligible to vote (Open, or Registered)
            return (
            <Alert className={`${alertBaseClasses} bg-blue-50 dark:bg-blue-950/20 border-blue-200 dark:border-blue-900/50`}>
                <AlertDescription className={alertTextClasses}>
                🗳️ Voting is now open! Go to the 'Vote' tab to cast your vote.
                </AlertDescription>
            </Alert>
            );
        }
    }

    return null; // Should not happen
  };

  // --- Render Logic ---

  if (loading) {
    return (
      // Use container for consistent centering and max-width during loading
      <div className="container max-w-5xl py-10 mt-20">
        <div className="space-y-6 animate-pulse">
          <div className="flex items-center gap-2">
            <Skeleton className="h-5 w-24" />
          </div>
           <Skeleton className="h-10 w-3/4 mx-auto" /> {/* Centered title skel */}
           <div className="flex justify-center gap-2">
                <Skeleton className="h-6 w-20" />
                <Skeleton className="h-6 w-24" />
                <Skeleton className="h-6 w-28" />
           </div>
          <Skeleton className="h-24 w-full" /> {/* Countdown skel */}
          <Skeleton className="h-12 w-full" /> {/* Status message skel */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
          </div>
           <Skeleton className="h-10 w-full" /> {/* Tabs list skel */}
          <Skeleton className="h-64 w-full" /> {/* Tab content skel */}
        </div>
      </div>
    );
  }

  if (error) {
    return (
      // Use container for consistent centering and max-width for error message
      <div className="container max-w-3xl py-10 mt-20 text-center">
         <Alert variant="destructive" className="text-left"> {/* Keep alert text left-aligned */}
           <AlertCircle className="h-4 w-4" />
           <AlertDescription>{error}</AlertDescription>
         </Alert>
         <div className="mt-6 flex justify-center gap-4">
            <Button variant="outline" onClick={() => router.push("/campaigns")}>
                <ArrowLeft className="h-4 w-4 mr-2" />
                Back to Campaigns
            </Button>
            <Button onClick={fetchCampaignData}>
                 <RefreshCw className="h-4 w-4 mr-2" />
                 Retry
            </Button>
         </div>
       </div>
     );
  }

  if (!campaignInfo) {
    // This case should ideally be covered by loading or error state, but as a fallback:
    return (
       <div className="container max-w-3xl py-10 mt-20 text-center">
         <Alert>
           <AlertCircle className="h-4 w-4" />
           <AlertDescription>Campaign data could not be loaded.</AlertDescription>
         </Alert>
         <Button variant="outline" className="mt-4" onClick={() => router.push("/campaigns")}>
           <ArrowLeft className="h-4 w-4 mr-2" />
           Back to Campaigns
         </Button>
       </div>
     );
  }

  // --- Main Render ---
  return (
    // Use container class for max-width and auto horizontal margins
    // Removed unnecessary flexbox centering classes from the main container
    <div className="container max-w-5xl py-10 mt-20"> {/* Adjusted max-width if needed */}
      {/* Back button */}
      <div className="mb-6">
        <Link
          href="/campaigns"
          className="inline-flex items-center text-sm text-muted-foreground hover:text-foreground"
        >
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Campaigns
        </Link>
      </div>

      {/* Campaign Title - Keep text-center here */}
      <div className="text-center mb-8">
        <h1 className="text-3xl md:text-4xl font-bold tracking-tight break-words">
          {campaignInfo.description}
        </h1>
         <div className="flex justify-center items-center flex-wrap gap-2 mt-3">
            <Badge variant={isDuringVoting ? "default" : isAfterEnd ? "secondary" : "outline"}
                   className={isDuringVoting ? "bg-green-600 hover:bg-green-700" : isAfterEnd ? "" : "text-blue-600 border-blue-300"} >
                {isBeforeStart ? "Upcoming" : isDuringVoting ? "Active" : "Ended"}
            </Badge>
            <Badge variant="outline">{campaignInfo.votingType === 0 ? "Candidate-Based" : "Proposal-Based"}</Badge>
            <Badge variant="outline">
            {campaignInfo.restriction === 0
                ? "Open To All"
                : campaignInfo.restriction === 1
                ? "Limited (Token Gated - Check Details)" // Clarify Limited type if applicable
                : "Registration Required"}
            </Badge>
             {/* Optionally add result type badge */}
             {/* <Badge variant="outline">{campaignInfo.resultType === 1 ? "Ranked Results" : "Standard Results"}</Badge> */}
        </div>
        <p className="text-sm text-muted-foreground mt-3">
            Created by: {formatAddress(campaignInfo.creator)}
        </p>
      </div>

      {/* Countdown Timer Card - Keep internal centering */}
      <Card className="mb-6 border-border/50 bg-background/50 backdrop-blur-sm">
        <CardContent className="pt-6 text-center">
          <h2 className="text-lg font-semibold mb-4 text-muted-foreground">
            {isBeforeStart ? "Voting Starts In" : isDuringVoting ? "Voting Ends In" : "Campaign Ended"}
          </h2>
          {!isAfterEnd ? (
            <CampaignCountdown
              endTime={isBeforeStart ? campaignInfo.startTime : campaignInfo.endTime}
              className="max-w-md mx-auto text-2xl md:text-3xl font-semibold" // Make countdown larger
            />
          ) : (
            <div className="text-lg font-medium text-muted-foreground">The voting period is over.</div>
          )}
        </CardContent>
      </Card>

      {/* Status Message */}
      <div className="mb-6">{getStatusMessage()}</div>

      {/* Campaign Stats Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 mb-8">
        <Card className="border-border/50 bg-background/50 backdrop-blur-sm">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <CalendarClock className="h-5 w-5 text-primary flex-shrink-0 mt-1" />
              <div>
                <h3 className="font-semibold text-base">Timeline</h3>
                <div className="text-sm text-muted-foreground mt-1 space-y-0.5">
                  <div>Start: {formatDate(campaignInfo.startTime)}</div>
                  <div>End: {formatDate(campaignInfo.endTime)}</div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-border/50 bg-background/50 backdrop-blur-sm">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <Users className="h-5 w-5 text-primary flex-shrink-0 mt-1" />
              <div>
                <h3 className="font-semibold text-base">Participation</h3>
                <div className="text-sm text-muted-foreground mt-1 space-y-0.5">
                  <div>Total Votes Cast: {campaignInfo.totalVotes}</div>
                  {/* Only show registered count if relevant */}
                  {campaignInfo.restriction !== 0 && <div>Registered: {campaignInfo.registeredVoterCount}</div>}
                  {/* Show item count */}
                   <div>{campaignInfo.votingType === 0 ? 'Candidates' : 'Proposals'}: {campaignInfo.itemCount}</div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-border/50 bg-background/50 backdrop-blur-sm">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <Clock className="h-5 w-5 text-primary flex-shrink-0 mt-1" />
              <div>
                <h3 className="font-semibold text-base">Your Status</h3>
                <div className="text-sm text-muted-foreground mt-1 space-y-0.5">
                    <div>Campaign: {isBeforeStart ? "Upcoming" : isDuringVoting ? "Active" : "Ended"}</div>
                    {isConnected ? (
                        <>
                            {campaignInfo.restriction !== 0 && (
                                <div>Eligibility: {userRegistered ? <span className="text-green-600">Eligible</span> : <span className="text-red-600">Not Eligible/Registered</span>}</div>
                            )}
                            <div>Vote Status: {userVoted ? <span className="text-green-600">Voted</span> : <span>Not Voted Yet</span>}</div>
                        </>
                    ) : (
                        <div>
                            <Button variant="link" size="sm" className="p-0 h-auto" onClick={connectWallet}>Connect wallet</Button> to check
                        </div>
                    )}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Tabs */}
      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
         <TabsList className="grid w-full grid-cols-2 sm:grid-cols-3 md:grid-cols-4 mb-6">
           <TabsTrigger value="overview">Overview</TabsTrigger>
           {/* Conditionally render Register Tab */}
           {isBeforeStart && campaignInfo.restriction === 2 && !userRegistered && isConnected && (
             <TabsTrigger value="register">Register</TabsTrigger>
           )}
            {/* Conditionally render Vote Tab */}
           {isDuringVoting && userRegistered && !userVoted && isConnected && (
               <TabsTrigger value="vote">Vote</TabsTrigger>
           )}
           {/* Conditionally render Results Tab */}
           {/* Show results if voting is active OR ended */}
           {/* OR if user is the owner (even before start) */}
           {(isDuringVoting || isAfterEnd || (isOwner && isConnected)) && (
             <TabsTrigger value="results">Results</TabsTrigger>
           )}
         </TabsList>

        <TabsContent value="overview">
          <OverviewTab
            campaignInfo={campaignInfo}
            proposals={proposals}
            candidates={candidates}
            isOwner={isOwner}
            isBeforeStart={isBeforeStart}
            isDuringVoting={isDuringVoting}
            isAfterEnd={isAfterEnd}
          />
        </TabsContent>

        {isBeforeStart && campaignInfo.restriction === 2 && !userRegistered && isConnected && (
          <TabsContent value="register">
            <RegisterTab
              campaignInfo={campaignInfo}
              contract={contract}
              campaignId={campaignId}
              isConnected={isConnected} // Pass isConnected
              connectWallet={connectWallet} // Pass connectWallet
              onRegistrationSuccess={handleRegistrationSuccess}
            />
          </TabsContent>
        )}

         {isDuringVoting && userRegistered && !userVoted && isConnected && (
          <TabsContent value="vote">
            <VoteTab
              campaignInfo={campaignInfo}
              proposals={proposals}
              candidates={candidates}
              contract={contract}
              campaignId={campaignId}
              isConnected={isConnected} // Pass isConnected
              connectWallet={connectWallet} // Pass connectWallet
              onVoteSuccess={handleVoteSuccess} // Ensure this is passed
            />
          </TabsContent>
        )}

         {(isDuringVoting || isAfterEnd || (isOwner && isConnected)) && (
          <TabsContent value="results">
            <ResultsTab
                campaignInfo={campaignInfo}
                proposals={proposals}
                candidates={candidates}
                isOwner={isOwner}
                // Pass time states if needed by ResultsTab
                isDuringVoting={isDuringVoting}
                isAfterEnd={isAfterEnd}
            />
          </TabsContent>
        )}
      </Tabs>
    </div>
  );
}